import React, { useContext, useState }  from "react";
import cartContext from "../../../context/cartContext";
import { navigate } from "gatsby";
import { gtag } from "../../../gtag";
import { H3 } from "../Header";

import { Button } from '@chakra-ui/react';
import EtsyLink from "../EtsyLink"
import Link from "../../components/Link";;

const defaultEtsyAffiliateLink = 'https://crochetnests.etsy.com';


const AddToCartButton = (props) => {
    const [ isAdding, setIsAdding] = useState(false);
    const [ hasExpressedInterest, setExpressedInterest] = useState(false);
    const { cart, updateQuantity } = useContext(cartContext);

    const {id, isBeta, price, priceId, etsyAffiliateLink, shouldRedirectToCart, title } = props;

    if (isBeta) {
        if (hasExpressedInterest) {
            return (
                <>
                    <H3 align='center' size='sm' fontSize='16px'>Thanks! Check back soon for updates on this item.</H3>

                    <Link variant="pink" href={defaultEtsyAffiliateLink}>Check out other items on Etsy</Link>
                </>
            );
        }
        return <Button
            variant={'pink'}
            disabled={true}
            onClick={() => { 
                gtag('event', `interested-${title}`);
                setExpressedInterest(true);
            }}>
                {`I'm interested in this`}
        </Button>
    }

    const addItem = () => {
        if (!priceId) {
            return;
        }
        const item = cart.find(item => item.priceId === priceId);
        if (!item) {
            updateQuantity(priceId, id, 1);
        } else {
            updateQuantity(priceId, id, item.quantity + 1);
        }
        setIsAdding(true);
        setTimeout(() => {
            setIsAdding(false);
            if (shouldRedirectToCart) {
                navigate("/cart")
            }
        }, 200);
    };

    const addToCartText = price ? `Add to cart | $${price}` : 'Add to cart';
    const text = isAdding ? 'Adding' : addToCartText;
    return (
        <>
            {/* <Button
                 variant={'pink'} 
                 disabled={!priceId || isAdding} 
                 onClick={addItem}>{text}
            </Button> */}
            {/* <H3 align='center' size='sm' fontSize='16px'>Other ways to buy</H3> */}
            <EtsyLink etsyLink={etsyAffiliateLink} />
        </>
    )
}

export default AddToCartButton;